import {
  Avatar,
  Box,
  Button,
  Icon,
  Link,
  Text,
  VStack,
} from "@chakra-ui/react";
import React from "react";
import { MdLocationOn } from "react-icons/md";
import { CONTENT_TYPES } from "../constants";
import andy from "../images/andyan.jpg";
import { getRandomColorScheme, useQuote } from "../utils";
import Badge from "./Badge";
import Card from "./Card";

const Sidebar = ({ contentType, setContentType, ...props }) => {
  const { data } = useQuote();

  return (
    <VStack {...props}>
      <Avatar name="Andy An" size="xl" src={andy} />
      <Text fontSize="lg">Andy An</Text>
      <Badge colorScheme={getRandomColorScheme()}>Software Engineer</Badge>
      <Badge colorScheme={getRandomColorScheme()}>Educator</Badge>
      <Link href="mailto:zheng.a@yahoo.com">
        <Text fontSize="sm">zheng.a@yahoo.com</Text>
      </Link>
      <Text fontSize="sm">
        <Icon as={MdLocationOn} />
        Hong Kong
      </Text>
      <Box py={10} width="100%">
        {Object.values(CONTENT_TYPES).map(c => (
          <Button
            isActive={c === contentType}
            onClick={() => setContentType(c)}
            isFullWidth
            variant="ghost"
          >
            {c}
          </Button>
        ))}
      </Box>
      {data && <Card>{data}</Card>}
    </VStack>
  );
};

export default Sidebar;
