import {
  Box,
  Image,
  Progress,
  Table,
  TableCaption,
  Tbody,
  Td,
  Th,
  Thead,
  Tooltip,
  Tr,
} from "@chakra-ui/react";
import React from "react";
import { experienceBase, techLogos } from "../data";
import { getPercentage } from "../utils";

const TechStackTable = props => {
  return (
    <Table variant="simple">
      <TableCaption>Technologies Andy has used for production</TableCaption>
      <Thead>
        <Tr position="sticky" top={0} bgColor="gray.800" zIndex={1}>
          <Th width={1 / 6}>Logo</Th>
          <Th width={2 / 6}>Name</Th>
          <Th width={3 / 6}>Experience</Th>
        </Tr>
      </Thead>
      <Tbody>
        {techLogos.map(t => (
          <Tr key={t.name}>
            <Td>
              <Image height="30px" alt={t.name} src={t.logo} />
            </Td>
            <Td>{t.name}</Td>
            <Td>
              <Tooltip label={`${t.experience} year(s)`}>
                <Box>
                  <Progress
                    value={getPercentage(experienceBase, t.experience)}
                  />
                </Box>
              </Tooltip>
            </Td>
          </Tr>
        ))}
      </Tbody>
    </Table>
  );
};

export default TechStackTable;
