import { Box, useTheme } from "@chakra-ui/react";
import React from "react";

const Card = ({ colorScheme = "green", ...props }) => {
  const theme = useTheme();
  return (
    <Box
      width="100%"
      border="1px"
      bgColor={`${colorScheme}.100`}
      color={`${colorScheme}.800`}
      boxShadow={`5px 5px 0 0 ${theme.colors[colorScheme][900]}`}
      rounded="lg"
      p={5}
      _hover={{
        boxShadow: `10px 10px 0 0 ${theme.colors[colorScheme][900]}`,
        position: "relative",
        right: 1,
        bottom: 1,
      }}
      {...props}
    />
  );
};

export default Card;
