import { Container, Grid, GridItem } from "@chakra-ui/react";
import React, { useState } from "react";
import { CONTENT_TYPES } from "../constants";
import Experience from "./Experience";
import Projects from "./Projects";
import Sidebar from "./Sidebar";
import TechStackTable from "./TechStackTable";

function Main() {
  const [contentType, setContentType] = useState(CONTENT_TYPES.EXPERIENCE);
  return (
    <Container maxW="4xl">
      <Grid
        templateColumns={["1fr", null, "1fr 2fr", "1fr 3fr"]}
        gap={4}
        mt={4}
        position="relative"
      >
        <GridItem>
          <Sidebar
            contentType={contentType}
            setContentType={setContentType}
            position="sticky"
            top={4}
          />
        </GridItem>
        <GridItem>
          {contentType === CONTENT_TYPES.EXPERIENCE && <Experience />}
          {contentType === CONTENT_TYPES.TECH_STACK && <TechStackTable />}
          {contentType === CONTENT_TYPES.PROJECTS && <Projects />}
          {contentType === CONTENT_TYPES.BLOG && "I'm working on it"}
          {contentType === CONTENT_TYPES.CV && "I'm working on it"}
        </GridItem>
      </Grid>
    </Container>
  );
}

export default Main;
