import { differenceInDays } from "date-fns";
import useSWR from "swr";

export const getPercentage = (total, portion) => {
  return ((portion / total) * 100).toFixed(2);
};

export const getYearsOfExperience = (since, lastUsed = Date.now()) =>
  (differenceInDays(lastUsed, since) / 365).toFixed(2);

const QUOTE_API =
  "https://api.forismatic.com/api/1.0/?method=getQuote&lang=en&format=json";

export const useQuote = () => {
  return useSWR(QUOTE_API);
};

export const getRandomColorScheme = () => {
  const colorSchemes = [
    "teal",
    "green",
    "pink",
    "blue",
    "red",
    "cyan",
    "orange",
    "yellow",
  ];
  return colorSchemes[Math.floor((Math.random() * 10) % colorSchemes.length)];
};
